.linkTopHeader {
    text-decoration: none;
    color: #fff;
}

.iconTopHeader {
    position: absolute;
    top: 50%;
    left: 0%;
    transform: translateY(-50%);
    font-size: 1rem;
    color: #fff;
}

.iconTopHeader svg {
    display: flex;
    align-items: center;
}

.linkTopHeader span {
    font-size: 1rem;
    padding: 10px 20px;
}

.header {
    width: 100%;
    background-size: cover;
    position: relative;
}

.toggle {
    padding: 1rem;
    color: #fff;
    text-align: center;
    position: absolute;
    top: 50%;
    left: 5%;
    transform: translateY(-50%) rotate(90deg);
    font-size: 4rem;
    display: none;
}

.type {
    position: relative;
}

.types {
    transform: translateY(10px);
    top: 100%;
    left: 70%;
    width: 15rem;
    position: absolute;
    height: fit-content;
    padding: 1rem;
    color: #fff;
    background: #1e3799e6;
    opacity: 0;
    transition: 0.5s;
    z-index: -2;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    border-bottom-left-radius: 50px;
    transform-origin: bottom right;
}

.types.active {
    z-index: 15;
    opacity: 1;
}

.types li {
    width: 100%;
    list-style: none;
    border-bottom: 1px solid #fff;
    margin-bottom: 0.5rem;
    cursor: pointer;
    padding: 0.3rem;
    transition: 0.3s;
}

.types li:hover {
    transform: translateY(-0.02rem);
    box-shadow: 0 0 0.5rem 0.01rem rgb(0, 0, 0, 0.5);
}
.liFirst{
    display: flex;
    justify-content: center;
}
.types li a {
    width: 100%;
    font-size: 1rem;
    font-weight: 900;
    display: flex;
    justify-content: flex-start;
    text-decoration: none;
    color: #fff;
}

.bestSeller {
    margin-left: 30px;
}





.topHeader {
    display: flex;
    width: 100%;
    height: 1.5rem;
    padding: 10px;
    background-color: rgb(30, 55, 153, 0.9);
    display: flex;
    align-items: center;
}

.leftTopHeader {
    display: flex;
    width: 50%;
}

.rightTopHeader {
    display: flex;
    width: 50%;
    justify-content: flex-end;
}

.bestSeller,
.blog,
.loveProduct,
.email,
.account {
    position: relative;
    padding: 0px 0.5rem;

}




.middleHeader {
    display: flex;
    width: 100%;
    color: #fff;
    height: 6rem;
    background-color: rgb(30, 55, 153, 0.9);
    /* position: relative; */
}

.leftMiddleHeader {
    width: 20%;
    display: flex;
    align-items: center;
}

.logo {
    display: flex;
    align-items: center;
    justify-content: center;
}

.logo h1 {
    display: flex;
    align-items: center;
    font-size: 2rem;
}

.logo img {
    width: 6rem;
    display: flex;
    align-items: center;
}

.centerMiddleHeader {
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
}

.listSearch {
    position: absolute;
    width: 50vh;
    top: 100%;
    left: 30%;
    background: rgb(30, 55, 153, 0.9);
    z-index: 25;
    padding: 1rem;
    display: none;
}

.listSearch.active {
    display: block;
}

.listSearch li {
    list-style: none;
    cursor: pointer;
    transition: 0.5s;
    padding: 0.5rem;
}

.listSearch li:hover {
    box-shadow: 0 0 0.1rem 0.05rem rgb(0, 0, 0, 0.5);
}

.filter {
    display: flex;
    border-right: 1px solid #000;
    height: 100%;
    position: relative;
}

.listFilter {
    position: absolute;
    top: 100%;
    left: 0%;
    display: none;
    z-index: 25;
    background: #fff;
    color: #000;
    width: 25vh;
}

.listFilter.active {
    display: block;
}

.listFilter li {
    list-style: none !important;
    width: 100%;
    display: flex;
    justify-content: flex-start;
    cursor: pointer;
}

.listFilter li:hover {
    box-shadow: 0 0 0.1rem 0.05rem #000;
}

.listFilter li span {
    font-size: 1rem;
}

.filter h1 {
    font-size: 1.2rem;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 0.5rem;
}

.filter span {
    font-size: 40px;
    display: flex;
    justify-content: center;
    height: 100%;
}

.filter span svg {
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    height: 100%;
}

.search {
    width: 80%;
    height: 3rem;
    display: flex;
    justify-content: space-around;
    align-items: center;
    border-radius: 50px;
    box-shadow: 0 0 2px 1px #fff;
}

.search input {
    padding: 0rem 1rem;
    width: 60%;
    height: 70%;
    border: none;
    border-radius: 50px;
    font-size: 1rem;
    color: #544540 !important;
}

.iconSearch {
    display: flex;
    justify-content: center;
    align-items: center;
}

.iconSearch svg {
    font-size: 1.5rem;
}

.elseMiddleHeader {
    /* position: absolute; */
    width: 20%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.cart {
    display: flex;
    justify-content: center;
    align-items: center;
    text-decoration: none;
    color: #000;
    position: relative;
}

.iconCart {
    color: #fff;
}

.iconCart sup {
    position: absolute;
    bottom: 80%;
    left: 100%;
}

.iconCart svg {
    font-size: 2rem;
}








.navbar {
    width: 100%;
    padding: 1px;
    height: 3rem;
    display: flex;
    align-items: center;
    color: #fff;
    background-color: rgba(243, 144, 31, 0.8);
}

.navbarContent {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.linkNavbar {
    text-decoration: none;
    color: #fff;
    height: 100%;
    padding: 1rem 3rem;
    margin-right: 1rem;
    display: flex;
    transition: 0.5s;
}

.linkNavbar:hover {
    background: #f3901fcc;
    box-shadow: 0 0 0.1rem 0.01rem rgb(30, 55, 153);
}

.linkNavbar h1 {
    font-size: 1rem;
    display: flex;
    height: 100%;
    align-items: center;
    transition: 0.5s;
}

.linkNavbar:hover h1 {
    color: rgb(30, 55, 153, 0.61);
    font-size: 1.2rem;
}

.author .linkNavbar {
    padding: 0px 10px !important;
}

.type .linkNavbar {
    padding: 0px 10px !important;
}

.type .linkNavbar h1 {
    display: flex;
    align-items: center;
    height: 100%;
}

.author span {
    font-size: 30px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 900;
}

.type span {
    font-size: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 900;
    transition: 0.5s;
}

.type span.active1 {
    transform: rotate(180deg);
}

.type span.active2 {
    transform: rotate(0deg);
}

.author span.active1 {
    transform: rotate(180deg);
}

.author span.active2 {
    transform: rotate(0deg);
}

.type span svg:hover {
    font-size: 2rem;
    animation: rotate 0.5s linear;
    animation-delay: 0.1s;
}

.author span svg:hover {
    font-size: 2rem;
    animation: rotate 0.5s linear;
    animation-delay: 0.1s;
}

@keyframes rotate {
    100% {
        transform: rotate(360deg);
    }
}

.type,
.author {
    display: flex;
    padding: 0 2rem;
    height: 100%;
}

.author {
    position: relative;
}

.author ul {
    width: 35vh;
    position: absolute;
    top: 100%;
    background: #1e3799e6;
    height: fit-content;
    padding: 1rem;
    opacity: 0;
    z-index: -2;
    transition: 0.7s;
    left: 65%;
    border-top-right-radius: 50px;
    border-bottom-right-radius: 50px;
    border-bottom-left-radius: 50px;
    transform-origin: bottom right;
    transform: translateY(10px);
}

.author ul.active {
    z-index: 15;
    opacity: 1;
}

.author ul li {
    width: 100%;
    list-style: none;
    margin-bottom: 0.5rem;
    border-bottom: 1px solid #fff;
    padding: 0.3rem;
    transition: 0.3s;
    cursor: pointer;
}

.author ul li:hover {
    transform: translateY(-0.02rem);
    box-shadow: 0 0 0.5rem 0.01rem rgb(0, 0, 0, 0.5);
}

.author ul li span {
    display: flex;
    justify-content: flex-start;
    font-size: 1rem;
}






.elseHeader {
    width: 100%;
    height: 84%;
}


@media only screen and (max-width : 412px) {
    .topHeader {
        display: none;
    }

    .middleHeader {
        height: 10vh;
        display: flex;
        justify-content: flex-end;
    }

    .navbar {
        display: none;
    }

    .leftMiddleHeader {
        display: flex;
        justify-content: center;
    }

    .centerMiddleHeader {
        display: none;
    }

    .logo h1 {
        font-size: 4rem;
    }

    .logo img {
        width: 12rem;
    }

    .toggle {
        display: block;
    }

    .elseMiddleHeader {
        width: 40%;
        display: flex;
        justify-content: flex-end;
        margin-right: 1rem
    }
    .iconCart{
        width: 2rem;
        height: 2rem;
    }
    .iconCart svg{
        font-size: 3rem;
    }
}




@media only screen and (min-width : 413px) and (max-width: 700px) {
    .topHeader {
        display: none;
    }

    .middleHeader {
        height: 15vh;
        display: flex;
        justify-content: flex-end;
    }

    .navbar {
        display: none;
    }

    .leftMiddleHeader {
        display: flex;
        justify-content: center;
    }

    .centerMiddleHeader {
        display: none;
    }

    .logo h1 {
        font-size: 2rem;
    }

    .logo img {
        width: 6rem;
    }

    .toggle {
        font-size: 2rem;
        display: block;
    }

    .elseMiddleHeader {
        width: 40%;
        display: flex;
        justify-content: flex-end;
        margin-right: 1rem
    }
}


@media only screen and (min-width : 701px) and (max-width: 1000px) {
    .topHeader {
        display: none;
    }

    .middleHeader {
        height: 15vh;
        display: flex;
        justify-content: flex-end;
    }

    .navbar {
        display: none;
    }

    .leftMiddleHeader {
        display: flex;
        justify-content: center;
    }

    .centerMiddleHeader {
        display: none;
    }

    .logo h1 {
        font-size: 2rem;
    }

    .logo img {
        width: 6rem;
    }

    .toggle {
        font-size: 2rem;
        display: block;
    }

    .elseMiddleHeader {
        width: 40%;
        display: flex;
        justify-content: flex-end;
        margin-right: 1rem
    }
}




@media (min-width:1921px) {
    .middleHeader {
        display: flex;
        width: 100%;
        color: #fff;
        opacity: 0.9;
        height: 8%;
        background-color: rgb(30, 55, 153, 0.8);

    }

    .linkNavbar {
        text-decoration: none;
        color: #fff;
        padding: 2rem;
        display: flex;
        height: 100%;
    }

    .linkNavbar h1 {
        font-size: 1.5rem;
        display: flex;
        align-items: center;
    }

    .rightMessageContent span {
        padding: 5px 0px;
        font-size: 1.8rem;
    }
}

@media only screen and (min-width: 1001px) and (max-width: 1300px) {
    .linkNavbar h1 {
        font-size: 0.7rem;
    }

    .linkNavbar:hover h1 {
        color: rgb(30, 55, 153, 0.61);
        font-size: 0.85rem;
    }

    .author ul {
        width: 30vh;
    }

    .author ul li span {
        font-size: 0.8rem;
    }
}